@import url("https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700;900&display=swap");

@import url("https://fonts.googleapis.com/css2?family=Open+Sans&display=swap");

* {
    margin: 0;
    padding: 0; }

body {
    width: 100%;
    margin: 0;
    padding: 0;
    color: #222222;
    font-family: "Lato", sans-serif;
    font-weight: 300; }

@import "header";

.text-description {
    display: flex;
    overflow: hidden; }

.slider {
    background-color: #e8e8e8;
    width: 100%;
    height: 614px;
    margin-bottom: 98px;

    &__brand {
        height: 614px;
        background-image: url(img/slider.png);
        background-position: right;
        background-repeat: no-repeat;
        display: flex;
        align-items: center; } }

.red-line {
    margin-right: 12px;
    width: 12px;
    background-color: #f16d7f; }

.slider-text {
    display: flex;
    text-transform: uppercase;
    line-height: 13px;

    &__right {
        display: block; } }

.brand-text {
    font-family: Lato;
    font-size: 60px;
    line-height: 1.2;
    font-weight: 900;
    letter-spacing: 0.025em;
    color: #222222; }

.luxFashion {
    font-family: Lato;
    font-size: 40px;
    line-height: 1.2;
    font-weight: 900;
    letter-spacing: 0.025em;
    color: #222222;

    & span {
        font-family: Lato;
        font-size: 40px;
        line-height: 1.2;
        font-weight: 900;
        letter-spacing: 0.025em;
        color: #f16d7f; } }

.category__men {
    background-image: url(img/banner1.png);
    width: 560px;
    height: 542px; }

.category__accessories {
    background-image: url(img/banner2.png);
    width: 560px;
    height: 261px; }

.category__women {
    background-image: url(img/banner3.png);
    width: 560px;
    height: 261px; }

.category__kids {
    background-image: url(img/banner4.png);
    width: 560px;
    height: 542px; }

.category {
    height: 823px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    &__left {
        display: flex;
        flex-direction: column;
        height: 823px;
        justify-content: space-between; }

    &__right {
        display: flex;
        flex-direction: column;
        height: 823px;
        justify-content: space-between; }

    & a {
        display: flex; }

    & a:hover {
        filter: drop-shadow(2px 4px 6px black);
        transition-duration: 0.2s; } }

.upperText {
    font-family: Lato;
    font-size: 18px;
    line-height: 1.2;
    font-weight: 900;
    text-transform: uppercase;
    letter-spacing: 0.025em;
    color: #222222; }

.lowerText {
    font-family: Lato;
    font-size: 30px;
    line-height: 1.2;
    font-weight: 900;
    text-transform: uppercase;
    letter-spacing: 0.025em;
    color: #f16d7f; }

.deal-text {
    background-color: #ffffff;
    margin-top: 25px;
    padding: 16px 20px;
    float: left; }

.featuredItems {
    margin-top: 100px;
    margin-bottom: 60px;
    text-align: center;
    height: 51px;

    &__head {
        font-family: Lato;
        font-size: 30px;
        line-height: 54px;
        font-weight: 700;
        letter-spacing: 0.025em;
        color: #222222;
        margin: 0;
        line-height: 20px; }

    &__text {
        font-family: Lato;
        font-size: 14px;
        line-height: 54px;
        font-weight: 400;
        letter-spacing: 0.025em;
        color: #9f9f9f; } }

.item__info {
    padding: 15px;

    &_name {
        font-family: Lato;
        font-size: 13px;
        line-height: 1.2;
        font-weight: 600;
        text-transform: uppercase;
        color: #222222; }

    &_price {
        font-family: Lato;
        font-size: 16px;
        line-height: 1.2;
        font-weight: 700;
        text-transform: uppercase;
        color: #f16d7f; } }

.catalogue {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: 25px; }

.item {
    width: 263px;
    margin-bottom: 40px;
    position: relative;
    transition-duration: 0.3s;

    &:hover {
        box-shadow: 0 0 15px #a4a4a4;
        transition-duration: 0.3s; }

    &-link {
        display: block;
        width: 263px;
        text-decoration: none; }

    &__photo {
        width: 100%;
        transition-duration: 0.3s; }

    &:hover .item__photo {
        filter: brightness(50%);
        transition-duration: 0.3s; } }

.addCart {
    display: none;
    justify-content: center;
    width: 100%;
    position: absolute;
    transition-duration: 0.3s;
    top: 98px;

    .item:hover & {
        display: flex;
        transition-duration: 0.3s; }

    &-link {
        display: flex;
        align-items: center;
        padding: 8px;
        border: 1px solid #ffffff;
        text-decoration: none;
        box-shadow: 0 5px 8px rgba(0, 0, 0, 0.16);
        transition-duration: 0.3s; }

    &-link:hover {
        box-shadow: 0 0 10px #ffffff;
        transition-duration: 0.3s; } }

.item-cart {
    width: 23px;
    height: 22px;
    color: #ffffff;
    margin-right: 10px; }

.addCart_text {
    font-family: Lato;
    font-size: 13px;
    line-height: 1.2;
    font-weight: 700;
    color: #ffffff; }

.browseProducts {
    width: 212px;
    height: 48px;
    background-color: #f16d7f;
    display: flex;
    justify-content: center;
    margin: auto;
    transition-duration: 0.3s;

    & a {
        display: flex; }

    &:hover {
        background-color: #ffffff;
        outline: 1px solid #f16d7f;
        transition-duration: 0.3s; }

    &:hover .browseProducts__text {
        color: #f16d7f;
        transition-duration: 0.3s; }

    &:hover .fa-long-arrow-alt-right {
        color: #f16d7f;
        transition-duration: 0.3s; }

    &__text {
        font-family: Lato;
        font-size: 16px;
        line-height: 54px;
        font-weight: 700;
        letter-spacing: 0.025em;
        color: #ffffff;
        align-self: center; } }

.fa-long-arrow-alt-right {
    font-family: FontAwesome;
    font-size: 15px;
    line-height: 54px;
    font-weight: 400;
    letter-spacing: 0.025em;
    color: #ffffff;
    margin-left: 10px;
    align-self: center; }

.womenOffer {
    display: flex;
    margin-top: 113px;
    margin-bottom: 97px;

    &__left {
        width: 767px;
        height: 529px;
        background-image: url(img/womenOffer.png);
        background-blend-mode: darken;
        background-color: #9b9da2; }

    &__right {
        width: 373px;
        height: 529px;
        background-color: #020305; }

    &__left_text {
        width: 293px;
        height: 78px;
        margin-top: 146px;
        margin-left: 39px; } }

.upperOffer {
    font-family: Lato;
    font-size: 53.7px;
    line-height: 1.2;
    font-weight: 700;
    letter-spacing: 0.025em;
    color: #ffffff;

    & span {
        font-family: Lato;
        font-size: 53.7px;
        line-height: 1.2;
        font-weight: 700;
        letter-spacing: 0.025em;
        color: #f16d7f;
        margin-left: 20px;
        text-transform: uppercase; } }

.lowerOffer {
    font-family: Lato;
    font-size: 32.2px;
    line-height: 1.2;
    font-weight: 700;
    letter-spacing: 0.025em;
    color: #ffffff; }

.upperOffer_right {
    font-family: Lato;
    font-size: 20px;
    line-height: 1.2;
    font-weight: 700;
    letter-spacing: 0.025em;
    color: #fbfbfb; }

.lowerOffer_right {
    font-family: Lato;
    font-size: 14px;
    line-height: 24px;
    font-weight: 300;
    letter-spacing: 0.025em;
    color: #fbfbfb;
    width: 219px;
    height: 58px; }

.womenOffer__right {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 529px;

    &_block {
        display: flex;
        justify-content: space-around; }

    &_text {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        width: 219px; }

    &_text h3 {
        font-family: Lato;
        font-size: 20px;
        line-height: 1.2;
        font-weight: 700;
        letter-spacing: 0.025em;
        color: #fbfbfb;
        padding-bottom: 10px; }

    &_text p {
        font-family: Lato;
        font-size: 14px;
        line-height: 24px;
        font-weight: 300;
        letter-spacing: 0.025em;
        color: #fbfbfb; } }

@import "footer";
